import React from "react";

import Layout from "../components/layout";
import SEO from "../components/seo";

const NotFoundPage = () => (
	<Layout>
		<SEO title="404: Not found" />
		<div style={{textAlign: "center", color: "#ff5781"}}>
			<h2 style={{fontSize: "3.6rem", marginBottom: "2rem"}}>NOT FOUND :(</h2>
			<p style={{fontSize: "2rem"}}>You just hit a route that doesn&#39;t exist... the sadness.</p>
		</div>
	</Layout>
)

export default NotFoundPage;